.title {
  font-weight: 700;
  font-size: 27px;
  line-height: 30px;
  color: $title-color;
  margin: 0;

  @media (min-width: $tablet-width) {
    font-size: 43px;
    line-height: 50px;
  }

  @media (min-width: $desktop-width) {
    font-size: 53px;
    line-height: 57px;
  }

  &--mini {
    @media (max-width: 319px) {
      font-size: 25px;
    }
  }
}
